import React from "react";
import {
  Grid,
  Item,
  Icon,
  Container,
  Divider,
  Header
} from "semantic-ui-react";
import styles from "../styles/style";
import me from "../asset/me.png";

const AboutMe = (prop) => {
  return (
    <div>
      <Grid>
        <Grid.Column width={11} style={styles.styleAboutMe}>
          <Item.Group>
            <Item>
              <Item.Image size="large" src={me} />
              <Item.Content>
                <Container style={styles.descAboutMe}>
                  <Divider hidden />
                  <Header as="h1">
                    <Header.Content>
                      I'm{" "}
                      <b onClick={prop.handleIsDownload} style={{ color: "teal" }}>
                        Haryana
                      </b>
                    </Header.Content>
                  </Header>
                  <Divider />
                  {prop.isDownload && (
                    <Header as="h4">
                      <Icon name="phone" />
                      <Header.Content>+62 8999 0836 73</Header.Content>
                    </Header>
                  )}
                  <Header as="h4">
                    <Icon name="envelope" />
                    <Header.Content>Hary.vai@gmail.com</Header.Content>
                  </Header>
                  {!prop.isDownload && (
                    <Header as="h4">
                      <a href="https://t.me/haryfun" target="blank">
                        <Icon
                          inverted
                          color="black"
                          name="telegram"
                          size="big"
                          link
                        />
                        <Header.Content>@Haryfun</Header.Content>
                      </a>
                    </Header>
                  )}
                  <Header as="h5">
                    <Icon name="globe" />
                    <Header.Content>
                      https://hary.fun/
                    </Header.Content>
                  </Header>
                  {prop.isDownload && (
                    <Header as="h4">
                      <Icon name="map marker alternate" />
                      <Header.Content>
                        Perum Wahana Cikarang blok A4 No.09 <br /> Desa Sukadami
                        Kec. Cikarang Selatan - Bekasi
                      </Header.Content>
                    </Header>
                  )}
                  <Divider hidden />
                </Container>
              </Item.Content>
            </Item>
          </Item.Group>
        </Grid.Column>
      </Grid>
      <Grid textAlign="center">
        <Grid.Column width={10} color="teal" style={{ marginBottom: "5em" }}>
          <span as="h3" style={{ color: "#fff" }}>
            Find Me on :{" "}
          </span>
          <a
            href="https://www.linkedin.com/in/haryana-28a85546/"
            target="blank"
          >
            <Icon inverted color="grey" name="linkedin" size="big" link />
          </a>
          <a href="https://github.com/haryanapnx" target="blank">
            <Icon inverted color="grey" name="github" size="big" link />
          </a>
          <a
            href="https://plus.google.com/100532462697926841010"
            target="blank"
          >
            <Icon inverted color="grey" name="google plus" size="big" link />
          </a>
          {/* <a href="https://www.instagram.com/haryfun_/" target="blank">
          <Icon inverted color="grey" name="instagram" size="big" link />
        </a> */}
        </Grid.Column>
      </Grid>
    </div>
  );
};

export default AboutMe;



